<template>
    <div class="navigator" @click="fHide">
        <ul class="nav" :class="active?'show':'not-show'" v-if="phone">
            <li><a href="#city">智慧城市</a></li>
            <li><a href="#drive">自动驾驶</a></li>
            <li><a href="#face">面部识别</a></li>
            <li><a href="#service">服务网络</a></li>
            <li id="demo"><a href="#aboutUs">关于我们</a></li>
        </ul>
        <ul class="nav" :class="active?'show':'not-show'" v-else>
            <li @click="gotarget(1)"><a>智慧城市</a></li>
            <li @click="gotarget(2)"><a>自动驾驶</a></li>
            <li @click="gotarget(3)"><a>面部识别</a></li>
            <li @click="gotarget(4)"><a>服务网络</a></li>
            <li @click="gotarget(5)" id="demo"><a>关于我们</a></li>
        </ul>
        <div class="switch" @click.stop="fActive" :style="active?'background-color:#fff;transition:all 0.5s':'background-color:#888;transition:all 0.5s'">
    		<span :class="active?'firstActive':'first'"></span>
    		<span :class="active?'secondActive':'second'"></span>
    		<span :class="active?'thirdActive':'third'"></span>
    	</div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      active: false,
      phone: false
    }
  },
  methods: {
    fActive: function () {
      this.active = !this.active
    },
    fHide: function () {
      this.active = false
    },
    gotarget (n) {
      this.$emit('gotarget', n)
    }
  },
  mounted () {
    if (window.innerWidth < 800) {
      this.phone = true
    }
  }
}
</script>
<style scoped>
/*默认宽度大于800*/
.navigator {
    width: 100%;
    height: 86px;
    background-color: #000;
	position: relative;
	z-index: 10;
	position: fixed;
	top: 0;
	left: 0
}
ul{
    margin: 0;
    padding: 0;
    width: 100%;
	list-style: none;
	box-sizing: border-box;
	font-size: 16px;
}

.nav {
	box-sizing: border-box;
	display: flex;
	height: 100%;
	justify-content: flex-end;
	align-items: center;
	padding-right: 45px;
	background: url(../assets/logowhite.png) no-repeat 34px center;
    background-size: 100px;
}
.nav li {
	margin-left: 18px;
	font-weight: bold;
	cursor: pointer;
}
a{
	text-decoration: none;
	color: #fff;
}
#demo {
	border: solid 2px #fff;
	padding: 8px 15px;
	border-radius: 50px;
}
.switch {
	display: none;
	cursor: pointer;
}
/*汉堡动画*/
.firstActive {
	transition: all 0.5s;
	transform: translate(0px,10px) rotate(45deg);
	background-color: #888!important;
}
.secondActive {
	transition: all 0.5s;
	opacity: 0;
	background-color: #888!important;
}
.thirdActive {
	transition: all 0.5s;
	transform: translate(0px,-10px) rotate(-45deg);
	background-color: #888!important;
}

/*屏幕小于800时*/
@media screen and (max-width: 800px) {
	.navigator {
		height: 48px;
		background: url(../assets/logoblue.png) no-repeat center center #fff;
		background-size: 50px;
		position: static;
	}
	.nav {
		display: block;
		transition: all 0.4s;
		position: fixed;
		padding: 0 24px 0 24px;
		background: #fff;
		height: 0px;
		overflow: hidden;
		left: 0;
		top: 0;
	}
	.nav a{
		color: #888;
		display: block;
		width: 100%;
		height: 100%;
	}
	.nav li {
		height: 46px;
		line-height: 46px;
		margin: 0;
		padding: 0 0 0 30px;
		border-top: solid 1px #eee;
		text-align: left;
	}

	#demo {
		height: 86px;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		border-top: 1px solid #eee;
		border-radius: 0;
	}
	#demo a {
		height: 46px;
		background-color: #2eb6dc;
		width: 100%;
		border-radius: 40px;
		text-align: center;
		color: #fff;
	}
	.show {
		height: 350px;
		padding: 74px 24px 0 24px;
	}
	.switch {
		display: block;
		position: fixed;
		width: 40px;
		height: 40px;
		background-color: #888888;
		left: 10px;
		top: 10px;
		border-radius: 5px;
	}
	.switch span {
		display: block;
		height: 2px;
		margin: 8px 6px;
		background-color: #fff;
		transition: all 0.5s;
	}
}
/*屏幕宽度大于1366时,定宽为1366居中*/
/*@media screen and (min-width: 1366px) {
	.nav{
		width: 1366px;
		margin: 0 auto;
	}
}*/
</style>



